import React from "react";

import ResumeColor64 from "../assets/resume-color-64.png";
import GithubColor64 from "../assets/github-color-64.png";
import LinkedInColor64 from "../assets/linkedin-color-64.png";
import MediumColor64 from "../assets/medium-color-64.png";

import ResumeBW64 from "../assets/resume-bw-64.png";
import GithubBW64 from "../assets/github-bw-64.png";
import LinkedInBW64 from "../assets/linkedin-bw-64.png";
import MediumBW64 from "../assets/medium-bw-64.png";

import ResumeColor32 from "../assets/resume-color-32.png";
import GithubColor32 from "../assets/github-color-32.png";
import LinkedInColor32 from "../assets/linkedin-color-32.png";
import MediumColor32 from "../assets/medium-color-32.png";

import ResumeBW32 from "../assets/resume-bw-32.png";
import GithubBW32 from "../assets/github-bw-32.png";
import LinkedInBW32 from "../assets/linkedin-bw-32.png";
import MediumBW32 from "../assets/medium-bw-32.png";

class Links extends React.Component {
  constructor() {
    super();
    this.state = {
      linkedIn: false,
      medium: false,
      github: false,
      resume: false
    };
  }

  isHovered = image => {
    this.setState({
      [image]: true
    });
  };

  stopHovering = image => {
    this.setState({
      [image]: false
    });
  };

  returnLinkedInImage() {
    if (this.props.size === "64") {
      if (this.state.linkedIn) {
        return <img src={LinkedInColor64} alt="" />;
      } else {
        return <img src={LinkedInBW64} alt="" />;
      }
    } else if (this.props.size === "32") {
      if (this.state.linkedIn) {
        return <img src={LinkedInColor32} alt="" />;
      } else {
        return <img src={LinkedInBW32} alt="" />;
      }
    }
  }

  returnGithubImage() {
    if (this.props.size === "64") {
      if (this.state.github) {
        return <img src={GithubColor64} alt="" />;
      } else {
        return <img src={GithubBW64} alt="" />;
      }
    } else {
      if (this.state.github) {
        return <img src={GithubColor32} alt="" />;
      } else {
        return <img src={GithubBW32} alt="" />;
      }
    }
  }

  returnMediumImage() {
    if (this.props.size === "64") {
      if (this.state.medium) {
        return <img src={MediumColor64} alt="" />;
      } else {
        return <img src={MediumBW64} alt="" />;
      }
    } else {
      if (this.state.medium) {
        return <img src={MediumColor32} alt="" />;
      } else {
        return <img src={MediumBW32} alt="" />;
      }
    }
  }

  render() {
    const LinkedIn = this.returnLinkedInImage();
    const Github = this.returnGithubImage();
    const Medium = this.returnMediumImage();
    return (
      <div className="row">
        <div className="col" />
        <div
          onMouseEnter={() => this.isHovered("github")}
          onMouseLeave={() => this.stopHovering("github")}
          className="mr-5"
        >
          <a target="-blank" href="https://github.com/JonathanYeeW">
            {Github}
          </a>
        </div>
        <div
          onMouseEnter={() => this.isHovered("linkedIn")}
          onMouseLeave={() => this.stopHovering("linkedIn")}
          className="mr-5"
        >
          <a target="-blank" href="https://www.linkedin.com/in/jonathanwyee">
            {LinkedIn}
          </a>
        </div>
        <div
          onMouseEnter={() => this.isHovered("medium")}
          onMouseLeave={() => this.stopHovering("medium")}
        >
          <a target="-blank" href="https://medium.com/@Jnothn">
            {Medium}
          </a>
        </div>
        <div className="col" />
      </div>
    );
  }
}

export default Links;
