import React from "react";

class Footer extends React.Component {
  state = {
    name: "",
    email: "",
    message: "",
    formSubmitted: false
  };

  onChangeHandler = event => {
    event.preventDefault();
    this.setState({
      [event.target.name]: event.target.value
    });
  };

  submitForm = () => {
    this.setState(prevState => {
      return {
        formSubmitted: !prevState.formSubmitted
      };
    });
    // const data = {
    //   name: this.state.name,
    //   email: this.state.email,
    //   message: this.state.message
    // };
    //connect to API
  };

  formSpreeFormReturn = formSubmitted => {
    if (formSubmitted) {
      return <p>your message has been sent!</p>;
    } else {
      return (
        <form
          method="POST"
          action="https://formspree.io/Jonathan.Yee.W@gmail.com"
        >
          <div className="form-group">
            <input
              type="email"
              name="email"
              placeholder="your email"
              className="form-control"
            />
          </div>
          <div className="form-group">
            <textarea
              name="message"
              rows="5"
              placeholder="your message"
              className="form-control"
            />
          </div>
          <button className="btn btn-outline-light" type="submit">
            Submit
          </button>
        </form>
      );
    }
  };

  // Possibly Deprecated Form
  returnFormView = formSubmitted => {
    if (formSubmitted) {
      return <p>your message has been sent!</p>;
    } else {
      return (
        <form onSubmit={event => event.preventDefault()}>
          <div className="form-group">
            <input
              type="text"
              className="form-control"
              name="name"
              value={this.state.name}
              onChange={this.onChangeHandler}
            />
          </div>
          <div className="form-group">
            <input
              type="text"
              className="form-control"
              name="email"
              value={this.state.email}
              onChange={this.onChangeHandler}
            />
          </div>
          <div className="form-group">
            <textarea
              type="text"
              className="form-control"
              name="message"
              rows="6"
              value={this.state.message}
              onChange={this.onChangeHandler}
            />
          </div>
        </form>
      );
    }
  };

  render() {
    // const formView = this.returnFormView(this.state.formSubmitted);
    const formView = this.formSpreeFormReturn(this.state.formSubmitted);
    return (
      <div id="Contact" className=" container">
        <h1>Contact</h1>
        <p className="secondary_text_color">
          Have a question or want to work together?
        </p>
        <div className="row">
          <div className="col" />
          <div className="col-5 contact_container">
            {formView}
            {/* <button className="btn btn-outline-light" onClick={this.submitForm}>
              Submit
            </button> */}
          </div>
          <div className="col" />
        </div>
      </div>
    );
  }
}

export default Footer;
