import React from "react";
import database from "../database/companies";
const { codeSnippets } = database;

class CodeSnippets extends React.Component {
  state = {
    pictures: codeSnippets,
    imageNumber: 0
  };

  nextImage = () => {
    this.setState(prevState => {
      if (prevState.imageNumber >= 3) {
        return {
          imageNumber: 0
        };
      } else {
        return {
          imageNumber: prevState.imageNumber + 1
        };
      }
    });
  };

  previousImage = () => {
    this.setState(prevState => {
      if (prevState.imageNumber < 1) {
        return {
          imageNumber: 3
        };
      } else {
        return {
          imageNumber: prevState.imageNumber - 1
        };
      }
    });
  };

  render() {
    return (
      <div id="code_snippet_container" className="tertiary_text_color bg-light">
        <h1 className="pb-5">Code Snippets</h1>
        <div className="row">
          <div className="col" />
          <div className="col-10">
            <div className="row">
              <div className="col text-left">
                <button
                  id="code_snippet_previous_button"
                  className="rounded"
                  onClick={this.previousImage}
                >
                  {"<<<"}
                </button>
              </div>
              <div className="col text-right">
                <button
                  id="code_snippet_next_button"
                  className="rounded"
                  onClick={this.nextImage}
                >
                  {">>>"}
                </button>
              </div>
            </div>
            <div id="code_snippet_box" className="border rounded">
              <img
                src={this.state.pictures[this.state.imageNumber]}
                alt=""
                className="code_snippet_image"
              />
            </div>
            {/* <div className=" bg-light rounded border">
              
              <div className="text-right">
                
              </div>
              
            </div> */}
          </div>
          <div className="col" />
        </div>
      </div>
    );
  }
}

export default CodeSnippets;
