import React from "react";
import BlogPosts from "../database/BlogPosts";

class Blog extends React.Component {
  state = {
    blogPosts: BlogPosts
  };

  render() {
    return (
      <div className="row p-5 fourth_color">
        {this.state.blogPosts.map((post, index) => {
          return (
            <div key={index} className="col">
              <Post post={post} />
            </div>
          );
        })}
      </div>
    );
  }
}

class Post extends React.Component {
  state = {
    isVisible: false
  };

  onHover = () => {
    this.setState({
      isVisible: true
    });
  };

  stopHover = () => {
    this.setState({
      isVisible: false
    });
  };

  render() {
    return (
      <div className="blog_box rounded">
        <img
          className="blog_image rounded"
          src={this.props.post.image}
          alt=""
        />
        <div className="text-left pl-3 pt-3">
          <h4>
            <a href={this.props.post.link} target="_blank">
              {this.props.post.title}
            </a>
          </h4>
          <h6>{this.props.post.date}</h6>
          <p>{this.props.post.intro}..</p>
        </div>
      </div>
    );
  }
}

export default Blog;
